a
	text-decoration: none

a:active
	color: rgba(31, 32, 65, 0.75)

.header__body
	background-color: #FFFFFF
	width: 100%
	height: 70px
	box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05)
	display: flex
	align-items: center
	justify-content: space-between

.header__logo
	margin: 15px 0px 15px 90px

.header__menu
	min-width: 450px
	width: 527px
	display: flex
	justify-content: space-between
	margin: 26px 0 26px 300px
	font-family: 'Montserrat', sans-serif
	font-size: 14px
	line-height: 17px
	color: rgba(31, 32, 65, 0.5)

.header-menu__home
	font-weight: bold

.services__image
	padding-left: 3px

.header__buttons
	min-width: 180px
	width: 200px
	margin: auto auto auto 25px
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: bold
	font-size: 12px
	line-height: 15px
	display: flex
	justify-content: space-between


.header__buttons
	&__login
		padding: 9px 20px 10px 20px
		border: 2px solid #BC9CFF
		border-radius: 22px
		color: #BC9CFF

	&__register
		padding: 9px 20px 10px 20px
		border: 2px solid #BC9CFF
		border-radius: 22px
		color: #FFFFFF
		background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)


